/* Main Navigation Drawer state styles */
.ig-nav-drawer {
  position: fixed;
  width: 18.75em;
  height: 100%;
  max-width: 90%;
  min-height: 100%;
  overflow-x: hidden;
  background-color: #fff;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 999;
  /*transition: all 0.5s cubic-bezier(0.55, 0, 0.1, 1);*/
  /* use full syntax, iOS Safari won't work with transition: width .3s ease-in, transform .3s ease-in; shorthand */
  transition-property: width, transform;
  transition-duration: 0.25s, 0.25s;
  transition-timing-function: ease-in-out, ease-in;
  transition-delay: 0s, 0s;
  box-shadow: 0px 8px 10px -5px rgba(0, 0, 0, 0.2), 0px 16px 24px 2px rgba(0, 0, 0, 0.14), 0px 6px 30px 5px rgba(0, 0, 0, 0.12);
  /* states */ }
  .ig-nav-drawer.collapsed {
    transform: translate3d(-300px, 0px, 0px);
    box-shadow: none; }
  .ig-nav-drawer.right {
    left: auto;
    right: 0; }
  .ig-nav-drawer.collapsed.right {
    transform: translate3d(300px, 0px, 0px);
    box-shadow: none; }
  .ig-nav-drawer.mini {
    width: 60px;
    transition-duration: 0.2s, 0.2s; }
  .ig-nav-drawer.pinned {
    position: relative;
    box-shadow: none; }
  .ig-nav-drawer.pinned.collapsed {
    transform: none;
    width: 0;
    padding: 0;
    overflow: hidden; }

