.ig-nav-drawer.panning {
  overflow-x: hidden;
  transition: none; }

.ig-nav-drawer-overlay.panning {
  transform: translate3d(0, 0, 0);
  /* try layer creation manually in advance for opacity */
  transition: none; }
  .ig-nav-drawer-overlay.panning.hidden {
    visibility: visible; }

.style-dummy {
